




















export default{
	data(){
		return{
			oldPaymentPwd:"",
			newPaymentPwd:"",
			confirmPaymentPwd:"",
			Signboard:'',
			Signguid:''
		}
	},
	onLoad:function () {
		var jsonList=uni.getStorageSync("jsonList");
		var data = JSON.parse(jsonList); //JSON字符串转对象
		this.Signboard = data.Ident_Signboard;
		this.Signguid = data.Ident_Signguid;
	},
	methods:{
		oldPayment:function(evt){
			this.oldPaymentPwd=evt.target.value;
		},
		newPayment:function(evt){
			this.newPaymentPwd=evt.target.value;
		},
		confirmPayment:function(evt){
			this.confirmPaymentPwd=evt.target.value;
		},
		changePayment(evt){
			if(this.oldPaymentPwd == '' || this.newPaymentPwd == '' || this.confirmPaymentPwd == ''){
				uni.showToast({
					title: '请将信息填写完整，不允许为空',
					icon:"none"
				})
			}else if ((!RegExp(/^[A-Za-z0-9]{5,16}$/).test(this.oldPaymentPwd)) || (!RegExp(/^[A-Za-z0-9]{5,16}$/).test(this.newPaymentPwd))||(!RegExp(/^[A-Za-z0-9]{5,16}$/).test(this.confirmPaymentPwd))) {
				uni.showToast({
					title: '密码格式不正确',
					icon:"none"
				})
			}else if (this.newPaymentPwd!=this.confirmPaymentPwd) {
				uni.showToast({
					title: '两次密码输入不一致',
					icon:"none"
				})
			}else{
				uni.request({
					url:'http://192.168.199.194/mobile/Modify/updatePayPwd',
					method:'GET',
					data:{
						oldpay:this.oldPaymentPwd,
						newpay:this.newPaymentPwd,
						Ident_Signboard: this.Signboard,
						Ident_Signguid: this.Signguid
					},
					success: (res) => {
						if(res.data.status==0){
							uni.showToast({
								title: res.data.message,
								icon:'none',
							})
						}else{
							uni.showToast({
								title: res.data.message,
								icon:'none',
							})
						}
					}
				})
			}
		}
	}
}
